<template>
  <!-- ----------------------------------------------------------------------------- -->
  <!-- DatatablesExpandableRows -->
  <!-- ----------------------------------------------------------------------------- -->
  <div>
    <v-list-item-subtitle class="text-wrap">
      The
      <code>show-expand</code> prop will render an expand icon on each default row. You can customize this with the
      <code>item.data-table-expand</code> slot. The position of this slot can be customized by adding a column with
      <code>value: 'data-table-expand'</code> to the headers array. You can also switch between allowing multiple expanded rows at the same time or just one with the
      <code>single-expand</code> prop. The expanded rows are available on the synced prop
      <code>expanded.sync</code>.
    </v-list-item-subtitle>
    <div class="mt-4">
      <v-data-table
        :headers="headers"
        :items="desserts"
        :single-expand="singleExpand"
        :expanded.sync="expanded"
        item-key="name"
        show-expand
        class="elevation-0 border"
      >
        <template v-slot:top>
          <v-toolbar flat>
            <v-toolbar-title>Expandable Table</v-toolbar-title>
